import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';

@Component({
    selector: 'app-using-documents',
    templateUrl: './using-documents.component.html',
    styleUrls: ['./using-documents.component.scss']
})
export class UsingDocumentsComponent implements OnInit {
    paramsList: Array<any> = [];

    constructor(
        public location: Location
    ) { }

    ngOnInit() {
        this.paramsList = [
            {
                name: 'progress',
                explain: '',
                type: 'number',
                optionaValue: '0-100',
                defaultValue: '-'
            },
            {
                name: 'type',
                explain: '',
                type: 'string',
                optionaValue: 'line/circle',
                defaultValue: 'line'
            },
            {
                name: 'lineHeight',
                explain: '',
                type: 'number',
                optionaValue: '',
                defaultValue: '6'
            },
            {
                name: 'textInside',
                explain: ` type=line `,
                type: 'string',
                optionaValue: 'outside,inside',
                defaultValue: 'outside'
            },
            {
                name: 'backGround',
                explain: '',
                type: 'string',
                optionaValue: '-',
                defaultValue: '#409EFF'
            },
            {
                name: 'textShow',
                explain: '',
                type: 'boolean',
                optionaValue: 'true/false',
                defaultValue: 'true'
            },
            {
                name: 'width',
                explain: '',
                type: 'number',
                optionaValue: '-',
                defaultValue: '126'
            },
            {
                name: 'status',
                explain: '',
                type: 'string',
                optionaValue: 'success/exception',
                defaultValue: '-'
            }
        ];
    };
};



